import _ from 'lodash'
import moment from 'moment'

import Auth from '../../mixins/auth'

import PlayerSearch from '../player-search'

import Utils from '../../utils'

export default {
  name: 'BetBox',

  mixins: [
    Auth
  ],

  components: {
    PlayerSearch
  },

  props: [
    'draw'
  ],

  data() {
    return {
      player: {
        agent_id: null,
        player_id: null,
        contact_no: null,
        draw_id: null,
        max_accumulated: null,
        combinations: []
      },
      playerRules: {
        player_id: [
          {
            required: true,
            message: 'The player field is required.',
            trigger: 'blur'
          },
          {
            required: true,
            message: 'The player field is required.',
            trigger: 'change'
          }
        ],
        contact_no: [
          {
            required: true,
            message: 'Please input contact # (ex. 09101234567)',
            trigger: 'blur'
          },
          // {
          //   validator(rule, value, callback) {
          //     if (value.length < 11) {
          //       callback(new Error('Invalid contact #'))
          //     } else {
          //       callback()
          //     }
          //   },
          //   trigger: 'change'
          // }
        ]
      },
      entry: {
        bet: 0,
        isramble: 'S',
        input: ''
      },
      entryRules: {
        input: [
          {
            required: true,
            message: 'Please input combination',
            trigger: 'blur'
          }
        ]
      },
      showDialog: false,
      inProgress: false
    }
  },

  computed: {
    maxInput() {
      let {
        numberformat,
        maxinput
      } = this.draw

      if (numberformat == '0-9') {
        return maxinput
      } else if (numberformat == '0-99') {
        return maxinput * 2
      }

      return 6
    },

    dialog() {
      return this.draw ? {
        title: this.draw.name,
        date: moment().format('MMMM DD, YYYY')
      } : null
    },

    validSend() {
      return this.player.player_id != null
        && this.player.contact_no
        && this.player.combinations.length
    },

    validClear() {
      return this.player.combinations.length > 0
    },

    validQueue() {
      let entry = this.entry

      return entry.input.length == this.maxInput
    }
  },

  mounted() {
    this.reset()
  },

  methods: {
    handleClose(done) {
      if (typeof done == 'function') {
        done()
      }
    },

    setContact(contact) {
      this.player.contact_no = contact
    },

    reset() {
      this.player = {
        agent_id: this.$user.id,
        player_id: null,
        contact_no: null,
        draw_id: this.draw.id,
        max_accumulated: this.draw.max_accumulated,
        combinations: []
      }

      this.entry = {
        bet: this.draw.minimumbet,
        isramble: this.draw.draw_type == '3d_rambol' ? 'S' : 'R',
        input: ''
      }
    },

    openDialog() {
      this.showDialog = true
    },

    closeDialog() {
      this.showDialog = false
      this.reset()
    },

    isNumber(evt) {
      evt = (evt) ? evt : window.event;
      var charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault()
      } else {
        return true
      }
    },

    removeEntry(entry) {
      let index = this.player.combinations.indexOf(entry)

      if (index > -1) {
        this.player.combinations.splice(index, 1)
      }
    },

    clearEntries() {
      this.player.combinations = []
    },

    async handleEntry() {
      let draw = this.draw
      let entry = this.entry

      if (draw.numberformat == '0-9') {
        let combination = this.entry.input.split('')
        let identifier = entry.input

        if (draw.draw_type == '3d_rambol') {
          if (entry.isramble == 'R') {
            let combArr = Utils.Math.permutation(combination)
            let clientIdentifier = _.sortBy(combArr
              .map(d => d.join('')))
              .join('|')
            let entries = combArr.map(d => {
              return {
                clientIdentifier,
                identifier,
                combination: d.join('-'),
                isramble: entry.isramble,
                bet: entry.bet
              }
            })

            let isMultiIdentifier = this.player.combinations.filter(c => {
              return c.clientIdentifier == clientIdentifier
            }).length > 0

            if (isMultiIdentifier) {
              throw new Error('You already entered same ramble combination')
            } else {
              this.player.combinations = this.player.combinations.concat(entries)
            }
          } else {
            this.player.combinations.push({
              identifier,
              combination: combination.join('-'),
              bet: entry.bet,
              isramble: entry.isramble
            })
          }
        } else {
          this.player.combinations.push({
            identifier,
            combination: combination.join('-'),
            bet: entry.bet,
            isramble: entry.isramble
          })
        }
      }
    },

    async queue() {
      if (!this.validQueue) return

      try {
        await this.handleEntry()

        this.entry.bet = this.draw.minimumbet
        this.entry.input = ''
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }
    },

    async send() {
      if (this.inProgress) return

      try {
        this.inProgress = true

        await this.$store.dispatch('bet/send', this.player)

        this.closeDialog()
        this.$message({
          type: 'success',
          message: 'Bet successful!'
        })
      } catch(e) {
        let { response } = e

        if (response) {
          let { data } = response

          if (   data
              && data.message) {
            if (_.isArray(data.message)) {
              data.message.forEach(message => {
                this.$message({
                  type: 'error',
                  message
                })
              })
            }
          } else {
            this.$message({
              type: 'error',
              message: e.message
            })
          }
        } else {
          this.$message({
            type: 'error',
            message: e.message
          })
        }
      }

      this.inProgress = false
    }
  }
}



